import React from "react";
import s from "./Post.module.css";

const Post = (props) => {
  return (
    <div className={s.item}>
      <img
        src="data:image/png;base64,"
        alt="Аватар"
      />
        {props.message}
      <div>{props.like} Like</div>
    </div>
  );
};

export default Post;
